import React from 'react'
import PropTypes from 'prop-types'
import { FormattedMessage, FormattedHTMLMessage } from 'react-intl'
import Layout from '../components/Layout'

import { withPrefix, Link } from "gatsby"
import SEO from '../Components/seo'
import SimpleSlider from '../Components/SimpleSlider'
import MailchimpSubscribe from '../Components/MailChimp'
import Plx from 'react-plx';
import MediaQuery from 'react-responsive';


/* wattys 2020 assets */
import topTear from "../images/top-tear-2020.png"
import sectionOneDivider from "../images/section-one-divider-2020.png"
import wattys2020Logo from "../images/watty-2020-logo.png"
import rocketShip from "../images/rocket-2020.png"
import sectionTwoBackground from "../images/section-two-background-2020.png"
import sectionTwoBackgroundMobile from "../images/section-two-background-mobile-2020.png"

import sectionTwoDivider from "../images/section-two-divider-background-2020-rev2.png"
import sectionThreeBottomDivider from "../images/section-three-divider-bottom-background-2020.png"
import sectionFourDividerDesktop from "../images/section-four-divider-background-2020.png"
import sectionFourDividerMobile from "../images/section-four-divider-mobile-background-2020.png"

import lowerTear from "../images/lower-tear-2020.png"
import clockOne from "../images/clock-one-2020.png"
import clockTwo from "../images/clock-two-2020.png"
import mailBox from "../images/mail-box-2020.png"
import wattysLetters from "../images/wattys-letters-2020.png"


import astroMobile from "../images/astro-mobile-2020.png"
import podCast from "../images/podcast-2020.png"
import sectionThreeRightSideTear from "../images/section-three-right-side-tear-2020.png"
import sectionThreeLeftSideTear from "../images/section-three-left-side-tear-2020.png"

import sectionSevenDivider from "../images/section-seven-divider-bottom-background-2020.png"
import sectionThreeWattysTextBottomRight from "../images/section-three-wattys-text-bottom-right-2020.png"
import sectionThreeWattysTextTopRight from "../images/section-three-wattys-text-top-right-2020.png"
import sectionThreeWattysWattysText from "../images/section-three-wattys-text-background-2020.png"
import sectionThreeDividerBottom from "../images/section-three-divider-bottom-2020.png"


import pastWinners from "../images/wattys-past-winners-2020.png"
/* end wattys 2020 assets */







const sectionTwoBackgroundAnimate = [
  {
    start: "self",
    duration: 500,
    properties: [
      {
        startValue:200,
        endValue: -150,
        property: "translateY"
      }
    ]
  },
];

const rocketShipAnimation = [
  {
    start: 'self',
    startOffset: 50,
    duration: 220,
    properties: [
      {
        startValue: 1,
        endValue: 1000,
        property: 'translateX',
      },
      {
        startValue: 1,
        endValue: -500,
        property: "translateY"
      },
    ],
  },
];

const mailBoxAnimation = [
  {
    start: "self",
    duration: 300,
    properties: [
      {
        startValue: 200,
        endValue: 0,
        property: "translateX"
      }
    ]
  },
];

const clockOneAnimation = [
  {
    start: "self",
    duration: 300,
    properties: [
      {
        startValue: 200,
        endValue: 0,
        property: "translateX"
      }
    ]
  },
];




const clockTwoAnimation = [
  {
    start: ".section-three",
    duration: 300,
    startOffset: -100,
    properties: [
      {
        startValue: -200,
        endValue: 350,
        property: "translateX"
      }
    ]
  },
];



const IndexPage = ({ pageContext: { locale } }) => (


  
  <Layout locale={locale}>
    <SEO title="The 2019 Watty Awards - Wattpad" keywords={[`Wattys`, `Wattpad`]} />


    <section className="" id="intro-section">
      <img src={topTear} alt="" className="top-tear "/>

      <div className="section-inner flex-col">
        
       
            
                <img src={wattys2020Logo} alt="" className="img-scale-larger"/>
                <h2 className="pink-txt font-one ">
                
                <FormattedMessage id="writersStrive" />
                </h2>  
     
      </div>
      
      <img src={sectionOneDivider} alt="" className="divider-bottom"/>



      <MediaQuery maxWidth={799}>
              {(matches) => {
                if (matches) {
                  return ( 
                    <div>                  
                      <img src={rocketShip} className="rocket-ship-mobile"/>
                    </div>           
                  ); 
                } else {
                  return ( 
                    <div className="testtets">
                      <Plx className='rocket-ship-container' parallaxData={ rocketShipAnimation}>
                        <img src={rocketShip} />
                      </Plx>
                    </div>     
                  ); 
                }
              }}
            </MediaQuery>



  

    </section>

    <section className="pink-bg section-two" id="">
      <div className="section-inner">
 
        <div className="flex">
 
            <div className="col">
               <img src={sectionTwoBackgroundMobile} className="show-on-mobile"/>

            </div>

            <div className="col">
              <p className="light-txt font-three purple-txt"><FormattedHTMLMessage id="aboutWattys" /></p>
              

            </div>


        </div>
  
      </div>
      <Plx
          className='section-two-background-animate hide-on-mobile' parallaxData={ sectionTwoBackgroundAnimate }>
            <img src={sectionTwoBackground} alt="About Wattys" />
      </Plx>

      
      <img src={sectionTwoDivider} draggable="false" alt="How To Enter" className="ps-ab divider-bottom"/>

    </section>



    <section className="section-three" id="">

            <MediaQuery maxWidth={799}>
              {(matches) => {
                if (matches) {
                  return ( 
                    <div>
                      <img src={clockOne} className="clock-one"/>
                      <img src={clockTwo} className="clock-two"/>
                    </div>           
                  ); 
                } else {
                  return ( 
                    <div>

                    <Plx className='clock-one' parallaxData={ clockOneAnimation}>
                      <img src={clockOne} className="clock-one"/>
                    </Plx>

                    <Plx className='clock-two' parallaxData={ clockTwoAnimation } >
                      <img src={clockTwo} className="clock-two"/>
                    </Plx>

                    </div>     
                  ); 
                }
              }}
            </MediaQuery>
            


      <div className="section-inner flex-col">

      <p className="regular-txt center-txt font-one pad-b pink-txt"><FormattedHTMLMessage id="countDown" /></p>

      <div className="wrapper-countdown">
      

        <div>
            <p class="regular-txt timer white-font font-four" id="days"></p>
            <p class="light-txt white-font font-one pad-t-medium"><FormattedMessage id="days" /></p>
        </div>
        <div>
            <p class="regular-txt timer white-font font-four" id="hours"></p>
            <p class="light-txt white-font font-one pad-t-medium"><FormattedMessage id="hours" /></p>       
        </div>

        <div >
        <p class="regular-txt timer white-font font-four" id="minutes"></p>
        <p class="light-txt white-font font-one pad-t-medium"><FormattedMessage id="minutes" /></p>
        </div>
      </div>


      
      </div>
      <img src={sectionThreeLeftSideTear} className="section-three-left-side-tear"/>
      <img src={sectionThreeRightSideTear} className="section-three-right-side-tear"/>
      <img src={sectionThreeWattysWattysText} className="section-three-WattysTextBackground"/>
      <img src={sectionThreeDividerBottom} className="section-three-bottom-divider" className="ps-ab divider-bottom"/>
      <img src={sectionThreeWattysTextTopRight} className="section-three-text-top-right"/>
      <img src={sectionThreeWattysTextBottomRight} className="section-three-text-bottom-right"/>

    </section>

    <section className="section-four pink-bg section-height-force" id="">

    <div className="section-inner flex-col">
 
    

     <div className="col">
       <h1 className="medium-txt font-one light-purple-txt pad-b-medium"><FormattedMessage id="submissionRequirementsHeader" /></h1>
       <p className="light-txt charcoal-txt width-70 font-two"><FormattedMessage id="submissionCopy" /></p>
       <div class="bullet-wrapper pad-tb-medium">
        <p className="light-txt font-three charcoal-txt"><FormattedMessage id="submissionPointOne" /></p>
        <p className="light-txt font-three charcoal-txt"><FormattedMessage id="submissionPointTwo" /></p>
        <p className="light-txt font-three charcoal-txt"><FormattedMessage id="submissionPointThree" /></p>
        <p className="light-txt font-three charcoal-txt"><FormattedMessage id="submissionPointFour" /></p>
       </div>
       
       
       <button className="learn-more-button darkpurple-bg white-txt">
              <FormattedMessage id="pastWinnersLink">{link => <a href={link} target="_blank"><FormattedMessage id="eligibilityBtn" /></a>}
              </FormattedMessage>
       </button>
     </div>

    </div>

    </section>


    <section className="section-five darkpurple-bg section-height-force-less" id="">
      <img src={sectionFourDividerDesktop} alt="" className="hide-on-mobile divider-top"/>
      <img src={sectionFourDividerMobile} alt="" className="show-on-mobile divider-top"/>


      <div className="section-inner">

        <div className="flex">

          <div className="col-70 hide-on-mobile">
            
          </div>
          <div className="col-30">

            <h1 className="medium-txt font-one pink-txt"><FormattedMessage id="newsHeader" /></h1>
            <p className="light-txt offwhite-txt pad-tb-medium font-three"><FormattedMessage id="newsCopy" /></p>
            <button className="learn-more-button pink-bg darkpurple-txt">
                  <FormattedMessage id="#">{link => <a href={link} target="_blank"><FormattedMessage id="newsBtn" /></a>}
                  </FormattedMessage>
          </button>

          </div>
        </div>

      </div>

      <Plx className='mail-box' parallaxData={ mailBoxAnimation } >
      
      <img src={mailBox} alt="" />

      </Plx>


      
    </section>

    <section className="section-six darkpurple-bg section-height-force-less" id="">

      <div className="section-inner">

        <div className="flex">

          <div className="col-70">
            <img src={pastWinners}/>
          </div>
          <div className="col-30">

            <h1 className="medium-txt font-one pink-txt"><FormattedMessage id="pastWinnersHeader" /></h1>
            <p className="light-txt offwhite-txt pad-tb-medium font-three"><FormattedMessage id="pastWinnersCopy" /></p>
            <button className="learn-more-button pink-bg darkpurple-txt">
                  <FormattedMessage id="#">{link => <a href={link} target="_blank"><FormattedMessage id="pastWinnersBtn" /></a>}
                  </FormattedMessage>
          </button>

          </div>
        </div>

      </div>
      <img src={wattysLetters} className="wattys-letters"/>
    </section>

    <section className="section-seven darkpurple-bg section-height-force-less" id="">

      <div className="section-inner flex-col">


          <div className="col-70">
            
          </div>
          <div className="col-30">

            <h1 className="medium-txt font-one pink-txt"><FormattedMessage id="roadToWattysHeader" /></h1>
            <p className="light-txt offwhite-txt pad-tb-medium font-three width-70"><FormattedMessage id="roadToWattysCopy" /></p>
            <button className="learn-more-button pink-bg darkpurple-txt">
                  <FormattedMessage id="#">{link => <a href={link} target="_blank"><FormattedMessage id="roadToWattysBtn" /></a>}
                  </FormattedMessage>
          </button>

          </div>
        </div>
        <img src={podCast} draggable="false" alt="How To Enter" className="podcast hide-on-mobile"/>

        <img src={sectionSevenDivider} draggable="false" alt="How To Enter" className="ps-ab divider-bottom"/>

    </section>
 
  </Layout>
)

IndexPage.propTypes = {
  pageContext: PropTypes.shape({
    locale: PropTypes.string.isRequired
  }).isRequired
}

export default IndexPage